import styled from 'styled-components';

const SkillsWrapper = styled.section`
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  width: 60%;
  padding: 1% 2% 3%;
  background-color: #fff;
`;

export default SkillsWrapper;



